import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Modal from '@material-ui/core/Modal'
import Backdrop from '@material-ui/core/Backdrop'
import Fade from '@material-ui/core/Fade'
import GeneralButton from '../../Button/GeneralButton'

const useStyles = makeStyles(theme => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3)
  }
}))

export default function TransitionsModal(props) {
  const classes = useStyles()
  const [open, setOpen] = React.useState(false)
  const [address, setValueAddress] = React.useState('')
  const [date, setValueDate] = React.useState('')

  const handleOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <div>
      <GeneralButton
        label="Send Invitation"
        testid="delegates-invite"
        handleClick={handleOpen}
      />
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <h2 id="transition-modal-title">Information</h2>
            <p id="transition-modal-description">
              Address :
              <input
                type="text"
                value={address}
                ref={props.setAddress}
                className="field"
                onChange={e => setValueAddress(e.target.value)}
              />
              <br />
              Date :
              <input
                type="text"
                value={date}
                ref={props.setDate}
                className="field"
                onChange={e => setValueDate(e.target.value)}
              />
            </p>
            <GeneralButton
              label="Invite"
              testid="delegates-invite"
              handleClick={props.handleClick}
            />
          </div>
        </Fade>
      </Modal>
    </div>
  )
}
